import { FC } from 'react';
import * as classes from './EventSalePresentationItem.module.scss'
import { ABadge } from '@/shared/ui/Badge/ABadge';
import { useAppDispatch } from '@/app/lib/hooks/redux';
import { eventSaleActions } from '@/entities/EventSale/model/EventSaleSlice';





const EventSalePresentationItemLabel: FC = ({ }) => {


    const dispatch = useAppDispatch()
    const back = () => {

        dispatch(
            eventSaleActions
                .setCurrentPresItem({ dealId: null, type: 'show' })
        )

    }


    return (<div className={classes.itemActionsWrapper}>
        <ABadge
            title={'связать'}
            color={'april'}
            size="small"
        // isIconeDone={true}

        />

        <ABadge
            title={'назад'}
            color={'orange'}
            size="small"
            isActive
            clickHendler={back}

        />
    </div>
    );
}

export default EventSalePresentationItemLabel;