import { FC } from 'react';
import * as classes from './EventSalePresentationList.module.scss'
import { ABadge } from '@/shared/ui/Badge/ABadge';
import { useAppDispatch, useAppSelector } from '@/app/lib/hooks/redux';
import { BXDeal } from '@/app/types/bitrix/bitrix-type';
import { eventSaleActions } from '../../model/EventSaleSlice';

const EventSalePresentationList: FC = ({ }) => {
    const dispatch = useAppDispatch();

    const showSalePresentation = (dealId: number) => {  //переход к показу элемента
        // dispatch(
        //     eventSaleActions.setCurrentPresItem({
        //         dealId,
        //         type: 'show'
        //     })
        // )

    }
    const setCurrentSalePresentation = (dealId: number) => { //связать презентацию с текущей продажей

        dispatch(
            dispatch(
                eventSaleActions.setCurrentPresItem({
                    dealId,
                    type: 'current'
                })
            )
        )

    }


    const sale = useAppSelector(state => state.eventSale)
    let presntationDeals = sale.presDeals.items;
    const currentPresntationDeal = sale.presDeals.current;
    

    return (
        <div className={classes.sale_presentations}>
            {presntationDeals && presntationDeals.length &&
                presntationDeals.map((deal: BXDeal, i: number) => {
                    const fullTitle = deal.TITLE
                    let title = fullTitle
                    // if (fullTitle.startsWith("Презентация")) {
                    //     title = fullTitle.replace(/^Презентация\s+/, '');
                    // }
                
                    const isActive = currentPresntationDeal && currentPresntationDeal.ID === deal.ID

                    const setPresentationSale = () => setCurrentSalePresentation(deal.ID)
                    return (

                        <div className={classes.itemWrapper}>
                            <p className={classes.itemTitle}
                                onClick={() => showSalePresentation(deal.ID)}

                            >{title}</p>


                            <ABadge
                                title={isActive ? '' : 'Связать'}
                                color={'april'}
                                size="small"
                                isActive={isActive}
                                isIconeDone={isActive}
                                clickHendler={setPresentationSale}

                            />
                            {/* <div className={classes.actionsWrapper}>
                                    <ABadge
                                        title={isActive ? '' : 'Связать'}
                                        color={'april'}
                                        size="small"
                                        isActive={isActive}
                                        isIconeDone={isActive}
                                        clickHendler={getPresentationSale}

                                    />
                                    <ABadge
                                        title={'Подробности'}
                                        color={'dark'}
                                        size="small"

                                    />
                                </div> */}

                        </div>)

                })
            }

        </div>
    );
}

export default EventSalePresentationList;