import { FC } from "react";
import * as classes from './ABadge.module.scss'
import IconeDoneSVG from '../../../assets/img/product-row/icon-done.svg'

interface ABadgeProps {
    title: string
    isActive?: boolean
    color?: 'green' | 'blue' | 'fiolet' | 'orange' | 'light' | 'dark' | 'april'
    | 'warning' | 'danger' | 'success'
    size?: 'small' | 'medium' | 'big'
    clickHendler?: (clickHendlerData: any | null) => void
    clickHendlerData?: { [key: string]: any }
    isIconeDone?: boolean


}


export const ABadge: FC<ABadgeProps> = ({
    title,
    isActive,
    color = "april",
    size = "medium",
    clickHendler,
    clickHendlerData,
    isIconeDone
}) => {

    const badgeClass = `${classes.abadge} ${classes[`abadge--${color}`]} 
    ${isActive ? classes.active : classes.inactive}
       ${isIconeDone ? classes.withIcone : ''} 
       ${classes[size]}
    
    `;



    return <div className={badgeClass} onClick={
        () => clickHendler && clickHendler(clickHendlerData || null)
    }>
        <p className={classes.title}>
            {title}
        </p>
        {/* <div className={classes.iconeDone}> */}
        {isIconeDone && <IconeDoneSVG
            width={'30px'}
            height={'30px'}
        // color={'color'} 
        />}
        {/* </div> */}
    </div >
}